const Event = (props) => {

    const {name, url, description} = props.info;

    let title = name.text;
    if(title.length > 100) {
        title = title.substr(0, 100) + '...';
    }

    let desc = description.text;
    if (desc) {
        desc = desc.substr(0, 250) + '...';
    }

    return (
        <p>desde evento</p>
    );
}
 
export default Event;


